@import './_banner';
@import './_feedback';

.banner {
  position: relative;
  background: unset;

  .banner-swiper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    z-index: -1;

    .swiper-slide {
      height: 100%;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  .swiper-pagination {
    position: static !important;
  }

  &__content {
    &--backText {
      top: -60px;
      left: -15px;

      @media(max-width: 992px) {
        top: -40px;
        left: -10px;
      }

      @media(max-width: 578px) {
        top: -30px;
      }
    }
  }
}

.section-design {
  padding-top: 140px;

  @media(max-width: 1280px) {
    padding-top: 120px;
  }

  @media(max-width: 992px) {
    padding-top: 80px;
  }

  @media(max-width: 578px) {
    padding-top: 50px;
  }
}

.swiper-design {
  position: relative;
  overflow: visible !important;

  .swiper-wrapper {
    margin-bottom: 50px;

    @media(max-width: 578px) {
      margin-bottom: 30px;
    }
  }

  .swiper-design-button-prev,
  .swiper-design-button-next {
    position: absolute;
    z-index: 1;
    top: 50%;
    margin-top: -50px;
    width: 48px;
    height: 48px;
    background: #fff;
    padding: 0;
    border: none;
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    svg {
      path {
        stroke: #0F0F0F;
      }
    }

    &.swiper-button-disabled {
      background: rgba(0, 0, 0, 0.3);
      cursor: unset;

      svg {
        path {
          stroke: #fff;
        }
      }
    }
  }

  .swiper-design-button-prev {
    left: -24px;
  }

  .swiper-design-button-next {
    right: -24px;
  }

  .swiper-scrollbar {
    width: 100%;
    height: 5px;
    background: #A9A9A9;

    .swiper-scrollbar-drag {
      height: 5px;
      background: #868686;
    }
  }
}

.design-slide {
  position: relative;

  &__img {
    width: 100%;
    max-height: 679px;
    object-fit: cover;
  }

  &__caption {
    position: absolute;
    bottom: 28px;
    left: 50%;
    transform: translateX(-50%);
    background: rgba(23, 28, 41, 0.72);
    padding: 20px;
    font-size: 16px;
    font-weight: 400;
    color: #fff;
    line-height: 1;
    text-align: center;
  }
}

.section-tech {
  padding-top: 140px;

  @media(max-width: 1280px) {
    padding-top: 120px;
  }

  @media(max-width: 992px) {
    padding-top: 80px;
  }

  @media(max-width: 578px) {
    padding-top: 50px;
  }

  .tech {
    display: flex;
    align-items: center;
    gap: 119px;

    @media(max-width: 1280px) {
      gap: 100px;
    }

    @media(max-width: 992px) {
      gap: 80px;
    }

    @media(max-width: 724px) {
      gap: 40px;
      flex-wrap: wrap;
    }

    &__img {
      flex-grow: 1;

      img {
        width: 100%;
        height: auto;
      }
    }

    &__info {
      width: 20%;
      min-width: 20%;

      @media(max-width: 1280px) {
        width: 30%;
        min-width: 30%;
      }

      @media(max-width: 724px) {
        width: 100%;
      }
    }

    &__option {
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__title {
      margin-bottom: 11px;
      font-size: 24px;
      font-weight: 300;
      color: #272C38;
    }

    &__value {
      font-size: 48px;
      font-weight: 300;
      color: #272C38;

      @media(max-width: 992px) {
        font-size: 42px;
      }

      @media(max-width: 578px) {
        font-size: 36px;
      }
    }
  }
}

.section-safety {
  padding-top: 140px;

  @media(max-width: 1280px) {
    padding-top: 120px;
  }

  @media(max-width: 992px) {
    padding-top: 80px;
  }

  @media(max-width: 578px) {
    padding-top: 50px;
  }

  .safeties {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 10px;
    flex-wrap: wrap;
  }

  .safety {
    width: 32%;

    @media(max-width: 748px) {
      width: 100%;
      margin-bottom: 50px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__img {
      height: 316px;
      overflow: hidden;
      margin-bottom: 50px;

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        margin: 0 auto;
      }
    }

    &__title {
      font-size: 24px;
      font-weight: 700;
      color: #272C38;
      margin-bottom: 20px;
    }

    &__text {
      font-size: 20px;
      font-weight: 300;
      line-height: 1.4;
      color: #272C38;

      ul {
        padding: 0;
        margin: 0;

        li {
          font-size: 20px;
          line-height: 1.4;
          font-weight: 300;
          color: #272C38;
          list-style: disc;
          margin-left: 20px;
        }
      }
    }
  }
}

.feedback {
  margin-bottom: 140px;

  &--2 {
    padding-top: 140px;

    @media(max-width: 1280px) {
      padding-top: 120px;
    }

    @media(max-width: 992px) {
      padding-top: 80px;
    }

    @media(max-width: 578px) {
      padding-top: 50px;
    }
  }

  &__section {
    justify-content: space-between;
    gap: 30px;
  }

  &__content {
    flex-grow: 1;
  }

  &--2 .feedback__img {
    position: static !important;

    @media(max-width:768px) {
      width: 100%;
    }
  }
}